@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;overflow-x:hidden;font-size:16px;font-family:Raleway, sans-serif}

.globe-container,.globe{width:350px;height:350px;overflow:hidden}.globe-container{position:relative;display:inline-block;margin:30px;padding:50px;transform:scale(1.3)}.globe{position:relative;display:block;margin:0;padding:0;top:0;left:0}.globe-worldmap,.globe-worldmap-front,.globe-worldmap-back,.globe-sphere,.globe-outer-shadow,.globe-inner-shadow{position:absolute;display:block;margin:0}.globe-sphere,.globe-outer-shadow,.globe-inner-shadow{left:0;top:0;width:200px;height:200px;background-position:0 0;background-repeat:no-repeat}.globe-worldmap{left:0;top:0;width:200px;height:200px;overflow:hidden;border-radius:50%}.globe-worldmap-front,.globe-worldmap-back{left:0;top:0;width:1000px;height:200px;overflow:visible;background-image:url(/joblio/static/media/worldmap.66bb1c77.svg);background-repeat:no-repeat}.globe-inner-shadow{background-image:url(/joblio/static/media/inner_shadow.5aeeed35.svg)}.globe-worldmap-front{background-position:0px 0px;animation:textureSpinreverse 20s linear infinite}.globe-worldmap-back{background-position:0px -220px;animation:textureSpin 20s linear infinite}@keyframes textureSpin{from{transform:translateX(0)}to{transform:translateX(-47.5%)}}@keyframes textureSpinreverse{from{transform:translateX(-47.5%)}to{transform:translateX(0)}}

.header{width:100%;min-height:100vh;background-image:url(/joblio/static/media/header_bg.c48cae02.jpeg);background-position:center;background-size:cover;background-repeat:no-repeat;background-attachment:fixed;position:relative;color:#f0f0f0;z-index:0;display:flex;justify-content:center}.header__overlay{width:100%;min-height:100vh;position:absolute;top:0;bottom:0;background-color:rgba(0,0,0,0.7);z-index:-1}.header__container{width:80%;min-height:70vh;margin:0 auto;display:flex;justify-content:space-between;align-items:center;z-index:1}.header__title{width:40%}h1{font-size:4rem;text-transform:uppercase;font-family:'Arial', sans-serif;border-bottom:2px solid #00C5FF;padding-bottom:20px;margin:0 auto 50px}.header__title p{font-size:3rem;line-height:70px}p.header_text-decor{font-size:1.5rem;text-transform:none}.header__button{width:200px;height:50px;margin-top:20px;font-size:1.5rem;color:#fff;background-color:rgba(0,0,0,0);border:2px solid #00C5FF;border-radius:3px;cursor:pointer;outline:none;transition:ease-out 0.3s}.header__button:hover{color:#000000;border:5px solid #00C5FF;background-color:#00C5FF}.header__info{width:40%;overflow:hidden}

.main__features{width:100%;max-width:1500px;background-color:#f0f0f0;display:flex;justify-content:space-between;flex-wrap:wrap;padding:50px 80px;text-align:center}.main__features h4{font-size:1.4rem;font-weight:600;text-transform:uppercase}.main__features div{width:50%;margin:50px 0 50px}.icon{font-size:3rem;margin-bottom:50px}.icon path{fill:#362f2f;transition:ease-out .5s;cursor:pointer}.icon path:hover{fill:#00C5FF}

h2.main__title{width:50%;text-align:center;font-size:4rem;margin:100px auto;font-weight:700;color:#362f2f;border-bottom:2px solid #362f2f;padding-bottom:20px}h2.main__title span{font-size:5rem;margin-top:100px;font-weight:700;color:#00C5FF}.section-who{width:100%;min-height:500px;padding:50px;display:flex;justify-content:flex-end;background-image:url(/joblio/static/media/bg4.baa2cc93.jpg);background-position:center;background-size:cover;background-repeat:no-repeat;position:relative}.section-who__overlay{position:absolute;width:100%;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.4);z-index:2}.section-who__text-block{width:50%;color:#fff;z-index:3}.section-who__text-block p,.section-who__text-block span{font-size:1.3rem;line-height:3rem;margin-top:30px;font-family:Lato, sans-serif;background-color:#000000;padding:10px}.section-who__text-block span{color:#00C5FF;font-size:1.6rem}.section-what{width:100%;margin:0 auto 200px;background-color:#f0f0f0}.section-what__text-block{width:80%;margin:0 auto;display:grid;grid-template-areas:"text1 arrow1 text2"   "arrow2 arrow2 arrow2"   "text3 arrow3 text4" "arrow4 arrow4 arrow4" "text5 text5 text5";grid-template-columns:40% 20% 40%;grid-template-rows:auto 200px auto 200px auto}.text1{grid-area:text1}.text2{grid-area:text2}.text3{grid-area:text3}.text4{grid-area:text4}.text5{grid-area:text5}.arrow1{grid-area:arrow1}.arrow2{grid-area:arrow2;text-align:right}.arrow3{grid-area:arrow3}.arrow4{grid-area:arrow4}.section-what__text-item{border-left:2px solid #00C5FF;line-height:2rem;padding:0 20px}.section-what__text-item span{font-size:2.5rem;padding-right:5px}.arrow-icon{font-size:4rem}.arrow-icon path{fill:#00C5FF}.section-what__arrow-item{display:flex;justify-content:center;align-items:center}.section-what__arrow-item.arrow2{width:80%;justify-content:flex-end}.section-what__arrow-item.arrow4{width:60%;margin:0 auto;justify-content:flex-start}

