@import 'constants';

// main {
//   background-color: $bg-color;
// }
h2.main__title {
  width: 50%;

  text-align: center;
  font-size: 4rem;
  margin: 100px auto;
  font-weight: 700;
  color: $dark-grey;
  border-bottom: 2px solid $dark-grey;
  padding-bottom: 20px;
  span {
    font-size: 5rem;
    margin-top: 100px;
    font-weight: 700;
    color: $accent;
  }
}

// WHO WE ARE

.section-who {
  width: 100%;
  min-height: 500px;
  padding: 50px;
  display: flex;
  justify-content: flex-end;
  background-image: url('../../assets/bg4.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.section-who__overlay {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  left:0;
  right: 0;
  background-color: rgba($color: #000000, $alpha: 0.4);
  z-index: 2;
}

.section-who__text-block {
  width: 50%;
  color: #fff;
  z-index: 3;
  
}
.section-who__text-block p,
.section-who__text-block span {
  font-size: 1.3rem;
  line-height: 3rem;
  margin-top: 30px;
  font-family: Lato, sans-serif;
  background-color: #000000;
  padding: 10px;

}

.section-who__text-block span {
  color: $accent;
  font-size: 1.6rem;
}

//WHAT WE DO

.section-what {
  width: 100%;
  margin: 0 auto 200px;
  background-color: $bg-color;
}

.section-what__text-block {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "text1 arrow1 text2"  
                       "arrow2 arrow2 arrow2"  
                       "text3 arrow3 text4"
                       "arrow4 arrow4 arrow4"
                       "text5 text5 text5";
  grid-template-columns: 40% 20% 40%;  
  grid-template-rows: auto 200px auto 200px auto; 
}

.text1 {
  grid-area: text1;
}
.text2 {
  grid-area: text2;
}
.text3 {
  grid-area: text3;
}
.text4 {
  grid-area: text4;
}
.text5 {
  grid-area: text5;
}
.arrow1 {
  grid-area: arrow1;
}
.arrow2 {
  grid-area: arrow2;
  text-align: right;
}
.arrow3 {
  grid-area: arrow3;
}
.arrow4 {
  grid-area: arrow4;
}

.section-what__text-item {
  border-left: 2px solid $accent;
  line-height: 2rem;
  padding: 0 20px;
  span {
    font-size: 2.5rem;
    padding-right: 5px;
  }
}

.arrow-icon {
  font-size: 4rem;
  path {
    fill: $accent;
  }
}

.section-what__arrow-item {
 display: flex;
 justify-content: center;
 align-items: center;
}
.section-what__arrow-item.arrow2 {
  width: 80%;
  justify-content: flex-end;
}

.section-what__arrow-item.arrow4 {
  width: 60%;
  margin: 0 auto;
  justify-content: flex-start;
}
