@import 'constants';

.main__features {
  width: 100%;
  max-width: 1500px;
  background-color: $bg-color;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 50px 80px;
  text-align: center;
  h4 {
    font-size: 1.4rem;
    font-weight: 600;
    text-transform: uppercase;
  }
  div {
    width: 50%;
    margin: 50px 0 50px;
  }

}

.icon {
  font-size: 3rem;
  margin-bottom: 50px;
}

.icon path {
  fill: $dark-grey;
  transition: ease-out .5s;
  cursor: pointer;
  &:hover {
    fill: $accent;
  }
}



