@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;600;700&display=swap');
@import 'constants';

.header {
  width: 100%;
  min-height: 100vh;
  background-image: url('../../assets/header_bg.jpeg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  color: #f0f0f0;
  z-index: 0;
  display: flex;
  justify-content: center;
}

.header__overlay {
  width: 100%;
  min-height: 100vh;
  position: absolute;
  top: 0;
  bottom:0;
  background-color: rgba($color: #000000, $alpha: 0.7);
  z-index: -1;
}

.header__container {
  width: 80%;
  min-height: 70vh;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
}

.header__title {
    width: 40%;
    // text-align: center;
}

h1 {
  // color: $accent;
  font-size: 4rem;
  text-transform: uppercase;
  font-family: 'Arial', sans-serif;
  border-bottom: 2px solid $accent;
  // width: 300px;
  padding-bottom: 20px;
  margin: 0 auto 50px;
}

.header__title p {
  font-size: 3rem;
  // text-transform: uppercase;
  line-height: 70px;
}

p.header_text-decor {
  font-size: 1.5rem;
  text-transform: none;
}

.header__button {
  width: 200px;
  height: 50px;
  margin-top: 20px;
  font-size: 1.5rem;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid $accent;
  border-radius: 3px;
  cursor: pointer;
  outline: none;
  transition: ease-out 0.3s;
}

.header__button:hover {
  color: #000000;
  border: 5px solid $accent;
  background-color: $accent;
}

.header__info {
  width: 40%;
  overflow: hidden;
}